.feedback-wrapper {
  padding-top: 123px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include vp-767 {
    padding-top: 51px;
  }
}

.feedback-slider {
  display: flex;
  padding-top: 37px;
  margin-bottom: 120px;
  padding-left: 158px;
  padding-right:70px;

  @include vp-1199 {
    padding-left: 57px;
    margin-bottom: 100px;
  }
  @include vp-767 {
    padding-top: 42px;
    padding-left: 34px;

  }
}

.feedback-slider-wrapper {
  min-width: 560px;
  max-width: 638px;
  display: flex;
  position: relative;
  overflow: hidden;
  margin-left: 0;
  padding-right: 50px;

  @include vp-767 {
    min-width: 226px;
  }
}

.feedback-slide {
  background-color: #1C3374;
  color: $color-default-white;
  width: 560px;
  padding-left: 33%;
  box-sizing: border-box;
  padding-right: 10%;
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  background-image: url("../../img/author-desktop.png");
  background-repeat: no-repeat;
  background-position: 10% 32%;
  margin-left: -30px;


  @include vp-1199 {
    width: 566px;
    background-position: 12% 38%;
  }

  @include vp-767 {
    max-width: 226px;
    padding-right: 20%;
    margin-left: 0;
    height: 335px;
    padding-top: 47px;
    background-size: 31%;
    background-position: 24% 15%;
    margin-bottom: 0;
  }
}

.feedback-slide__name {
  font-size: 20px;
  font-weight: 700;
  line-height: 30px;
  width: 330px;

  @include vp-767 {
    width: 62px;
    align-self: end;
  }
}

.feedback-slide__text {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  width: 330px;
  margin-bottom: 35px;

  @include vp-767 {
    width: 176px;
    align-self: center;
    text-align: center;
  }
}

.feedback__button-prev {
  left: -19px;
}

.feedback__button-next {
  right: 10px;
  z-index: 12;
}
