.membership {
  background-color: #F8F9FC;
  position: relative;
  margin-bottom: -67px;
}

.membership-wrapper {
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin-bottom:3px;

  @include vp-1199 {
    padding-top: 100px;
  }

  @include vp-767 {
    padding-top: 63px;
  }
}

.membership__title {
  margin-left: -3px;
  margin-bottom: 10px;
}

.weight {
  position: absolute;
  width: 285px;
  height: 353px;
  background-image: url("../../img/weight.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  bottom: -82px;
  right: 0;

  @include vp-1199 {
    width: 341px;
    height: 403px;
    bottom: -49px;
    background-image: url("../../img/weight-tablet.png");
  }

  @include vp-767 {
    display: none;
  }
}
